body {
  height: 100%;
}

.parent-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main-content {
  flex: 1;
  background: linear-gradient(-45deg, #7A4085, #5E2B99, #F75EFF,
      #CA38D1, #9400D1, #B394D6, #5D0085);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
}

.auth {
  background-color: #282c34;
  color: aliceblue;
  padding: 15px;
  border-radius: 15px;
}

.btn_submit {
  margin-bottom: 15px;
  padding-left: 60px;
  padding-right: 60px;
}


.dashboard {
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  padding-bottom: 20px;
  padding-top: 20px;
  background: linear-gradient(-45deg, #6f3aab, #850fb8);
}

@keyframes gradient {
  /* main gradient. */
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}


.navigation {
  background-color: aliceblue;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 10px;
}

.dash_main {
  margin-top: 15px;
  background-color: aliceblue;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 10px;
}

.file_ul {
  margin-top: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 10px;
  border: 1px;
  border-style: solid;
  border-color: #9400D1;
  padding-right: 15px;
}

.file_upload {
  width: 100%;
  height: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
}

.file_upload_bg {
  min-height: 100px;
  height: 450px;
  position: relative;
}

.center_middle {
  position: absolute;
  top: 30%;
  right: 25%;
  left: 25%;
}

.btn_upload {
  width: 30%;
}


.vertical-center {
  margin: 0;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.file_item {
  padding-top: 5px;
  padding-bottom: 5px;
}

.upload_secion {
  position: relative;
}

.upload_section_row {
  position: relative;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-top: 10px;
}

.total_files {
  text-align: right;
  background-color: #9400D1;
  padding: 5px;
  border-radius: 20px;
  color: whitesmoke;
}

.test {
  background-color: #61dafb;
}

.file_details {
  background-color: white;
  width: 100%;
  border-radius: 15px;
  margin: 5px;
  padding-top: 10px;
  animation: fadeInFromNone 1s ease-out;
}

@keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0;
  }

  1% {
    display: block;
    opacity: 0;
  }

  50% {
    display: block;
    opacity: 50;
  }

  100% {
    display: block;
    opacity: 1;
  }
}

.right_align {
  position: absolute;
  right: -20%;
  margin-right: 10px;
}

.upload_status {
  margin-top: 20px;
}

.progress_bar {
  height: 20px;
}

.focus {
  font-weight: bold;
  text-shadow: whitesmoke;
  font-size: large;

}

.entry:hover {
  transition-duration: 1s;
  transition-property: font-size;
  font-size: 25px;
}

.entry {
  transition-duration: 1s;
  transition-property: font-size;
}

.hidenseek {
  /* dont add anything else in this css component. it's used otherplaces as well. */
  visibility: collapse;
}

.alert-space {
  margin-top: 10px;
  margin-bottom: 10px;
}

.upload_share_email {
  margin-top: 15px;
  margin-bottom: 15px;
  animation: fadeInFromNone 3s ease-out;
}

.list_of_files {
  max-height: 100vh;
  padding-right: 20px;
  padding-bottom: 10px;
}

.img_center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.share_box {
  display: block;
  animation: fadeInFromNone 2s ease-out;
}

.display_none {
  display: none;
  animation: fadeInFromNone 2s ease-out;
}


.file_list_active {
  background-color: #9400D1;
  color: whitesmoke;
}

.file_list_passive {
  background-color: grey;
  color: whitesmoke;
}

.footer {
  background: linear-gradient(-45deg, #6f3aab, #850fb8);
}